.fotoPerfil{
    border-radius: 100px;
    width: 200px;
    height: 200px;
    margin: 20px;
}

h5{
    font-family: 'Neucha', cursive;
    margin: 10px;
    color: var(--colorTexto);
    font-weight: bold;
    font-size: large;
}

.mainPerfil{
    display: flex;
    
}

.infoContainer{
    width: 600px;
    margin-left: 100px;
    margin-bottom: 50px;
    border: 2px solid black;
    box-shadow: 0 5px 5px  black;
}

.InfoSportContainer{
    margin: 10px;
    justify-content: space-around;
    align-items: center;
    display: flex;
}

.btnCategoria{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    box-shadow: 5px 5px 10px black;
    border: none;
    width: 200px;
    height: 50px;
}

.h1Perfil{
    font-family: Arial, Helvetica, sans-serif;
}

